.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: auto;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.actions {
  text-align: right;
  display: flex;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #8a2b06;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
  margin-left: auto;
}

.actions button:hover,
.actions button:active {
  border-color: #5a1a01;
  color: white;
}

.actions .button--alt {
  background-color: black !important;
}

.actions .button {
  background: rgb(52 124 40) -6%;
  color: white;
  margin-left: 12px;
}

.cart-items h1 {
  font-size: 24px;
  font-weight: 600;
  font-family: Poppins;
}
